html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 2.8rem;
}
@media (min-width: 768px) {
  body.scroll-Nav {
    padding-top: 84px;
  }
}

h1, h2, h3, h4 {
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.8rem;
}

p {
  font-size: 2.2rem;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.bloque__flecha {
  display: flex;
  justify-content: center;
  margin: 1rem 0;
}
.bloque__flecha a {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: rgba(14, 138, 150, 0.6);
}

@media (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header.nav-fijo {
    background-color: rgba(51, 46, 46, 0.925);
    color: #0e8a96;
    box-shadow: 1px 3px 5px #000;
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
  }
}
.header h1 a {
  font-weight: 700;
  font-size: 5rem;
  filter: opacity(90%);
  margin: 2rem 0;
  color: #83c5be;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 4rem 0;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    margin: 0rem;
  }
}
.navegacion a {
  font-weight: 700;
  font-size: 2.2rem;
  color: #0e8a96;
}

.hero {
  position: relative;
  background-image: url("/build/img/aerostaticos-glob.avif");
  background-size: cover;
  background-position: center center;
  height: 90rem;
}

.hero__contenido {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.hero__contenido .hero__informacion {
  border-radius: 2rem;
  padding: 3rem;
  margin-top: 15rem;
  height: auto;
  background-color: rgba(0, 0, 0, 0.7);
}
@media (min-width: 768px) {
  .hero__contenido .hero__informacion {
    width: 65rem;
  }
}
.hero__contenido .hero__informacion h2 {
  color: #83c5be;
  font-size: 5rem;
  line-height: 4rem;
  margin: 0;
  text-align: left;
  width: 90%;
}
.hero__contenido .hero__informacion p {
  padding: 3rem 0;
  color: #fff;
}
.hero__contenido .hero__informacion .btn {
  border-radius: 1rem;
  border: 0.3rem solid #83c5be;
  color: #83c5be;
  padding: 1rem;
  background: transparent;
}
.hero__contenido .hero__informacion .btn:hover {
  cursor: pointer;
}

.aportaciones {
  padding: 5rem;
  background-color: #0e8a96;
}
@media (min-width: 768px) {
  .aportaciones .aportaciones__bloques {
    margin-top: -20rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .aportaciones .aportaciones__bloques {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.aportaciones__bloque {
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 2rem;
  margin-bottom: 3rem;
  border-radius: 2rem;
  display: grid;
  place-items: center;
}
.aportaciones__bloque .icono__img {
  background-color: #0e8a96;
  padding: 2rem;
  border-radius: 50%;
}
.aportaciones__bloque .icono__img img {
  width: 5rem;
  height: 5rem;
}
.aportaciones__bloque .aporatacion__informacion h2 {
  text-transform: uppercase;
}
.aportaciones__bloque .aporatacion__informacion p {
  text-align: center;
}

.experiencia {
  padding: 5rem 0;
  background-color: #000;
}
.experiencia h2 {
  color: #fff;
}
@media (min-width: 768px) {
  .experiencia .experiencias__iconos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .experiencia .experiencias__iconos .experiencias__icono:nth-child(3) {
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .experiencia .experiencias__iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  .experiencia .experiencias__iconos .experiencias__icono:nth-child(3) {
    grid-column: auto;
  }
}

.experiencias__icono {
  display: grid;
  place-items: center;
}
.experiencias__icono picture {
  padding: 3rem;
}
.experiencias__icono picture img {
  border-radius: 50%;
  width: 25rem;
  height: 25rem;
}

.experiencia__informacion h3 {
  color: #fff;
}
.experiencia__informacion .boton {
  text-align: center;
}
.experiencia__informacion .btn {
  border-radius: 1rem;
  border: 0.3rem solid #83c5be;
  color: #83c5be;
  padding: 1rem;
  background: transparent;
  font-size: 2rem;
}
.experiencia__informacion .btn:hover {
  cursor: pointer;
}

.destinos {
  background-color: #0e8a96;
  padding: 5rem 0;
}
.destinos h2, .destinos p {
  color: #fff;
  text-align: center;
  padding: 2rem 0;
}
.destinos .centrar {
  max-width: 75rem;
  margin: 0 auto;
}

.aventuras {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .aventuras {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .aventuras {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
  }
}

.aventura {
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 5px -10px 2px rgba(255, 255, 255, 0.4);
  padding: 1rem;
  margin-bottom: 3rem;
  border-radius: 2rem;
  display: grid;
  place-items: center;
}
.aventura p {
  color: #000;
  text-align: center;
}
.aventura picture img {
  border-radius: 50%;
  width: 20rem;
  height: 20rem;
}
.aventura .btn {
  border-radius: 1rem;
  border: 0.3rem solid #0e8a96;
  color: #0e8a96;
  padding: 1rem;
  background: transparent;
  width: 30%;
  text-align: center;
  font-size: 2.2rem;
  margin-top: 3rem;
}
.aventura .btn:hover {
  cursor: pointer;
}

.oceano__imagen {
  position: relative;
  background-image: url("/build/img//publicidad/lancha.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  height: 80rem;
}

.oceano__informacion {
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  color: #fff;
  border-radius: 2rem;
  padding: 3rem;
  margin-top: 5rem;
  top: 0;
  left: 0;
  right: 0;
}
@media (min-width: 768px) {
  .oceano__informacion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.oceano__informacion .oceano__titulo h3 {
  text-align: right;
  font-size: 5rem;
  line-height: 4.4rem;
}
.oceano__informacion .oceano__texto {
  text-align: center;
}

.ofertas {
  position: relative;
  background-image: url(/build/img/publicidad/bosque.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 60rem;
}
.ofertas h2 {
  font-size: 5rem;
  line-height: 4.5rem;
  color: #fff;
  margin: 0;
  padding: 8rem 0;
}
.ofertas .subtitulo {
  max-width: 85rem;
  min-width: 25rem;
  margin: 0 auto;
  text-align: center;
  color: #0e8a96;
  font-size: 3.8rem;
  line-height: 4rem;
  margin: 2rem 0;
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ofertas__campos {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .ofertas__campos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 1024px) {
  .ofertas__campos {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
}

.ofertas__campo {
  background-color: rgba(131, 197, 190, 0.2);
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 2rem;
  color: #fff;
}
.ofertas__campo .descuento {
  font-size: 8rem;
}
.ofertas__campo p {
  color: #fff;
  font-size: 2rem;
  text-align: center;
}

.viajes {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .viajes {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .viajes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.viajes .viajes__viaje:nth-child(1),
.viajes .viajes__viaje:nth-child(2),
.viajes .viajes__viaje:nth-child(3) {
  color: #fff;
  text-align: center;
  margin-bottom: 3rem;
  position: relative;
  background-repeat: repeat;
  background-size: cover;
  background-position: center center;
  height: 70rem;
}
.viajes .viajes__viaje:nth-child(1) .viaje__informacion,
.viajes .viajes__viaje:nth-child(2) .viaje__informacion,
.viajes .viajes__viaje:nth-child(3) .viaje__informacion {
  padding: 2rem;
}
.viajes .viajes__viaje:nth-child(1) {
  background-image: url("/build/img/viajes/viajes1.webp");
}
.viajes .viajes__viaje:nth-child(1) .viaje__informacion {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  left: 0;
  right: 0;
  bottom: 0;
}
.viajes .viajes__viaje:nth-child(2) {
  background-image: url("/build/img/viajes/viajes2.webp");
}
.viajes .viajes__viaje:nth-child(2) .viaje__informacion {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  left: 0;
  right: 0;
  top: 0;
}
.viajes .viajes__viaje:nth-child(3) {
  color: #000;
}
@media (min-width: 768px) {
  .viajes .viajes__viaje:nth-child(3) {
    height: auto;
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .viajes .viajes__viaje:nth-child(3) {
    grid-column: auto;
  }
}

.viajes__viaje p {
  margin: 3rem 0;
}
.viajes__viaje .btn {
  margin-top: 2rem;
  border-radius: 1rem;
  border: 0.3rem solid #0e8a96;
  color: #0e8a96;
  padding: 1rem;
  background: transparent;
}
.viajes__viaje .btn:hover {
  cursor: pointer;
}

.color-bg {
  background-color: #0e8a96;
  padding: 5rem;
  height: 40rem;
}

.actividades {
  margin-top: -40rem;
}
.actividades h2 {
  color: #fff;
  padding-top: 10rem;
}

@media (min-width: 768px) {
  .actividades__bloques {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .actividades__bloques .actividades__bloque:nth-child(3) {
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .actividades__bloques {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  .actividades__bloques .actividades__bloque:nth-child(3) {
    grid-column: auto;
  }
}

.actividades__bloque {
  padding: 2rem;
}
.actividades__bloque picture img {
  border-radius: 2rem;
  height: 40rem;
  width: 100%;
}
.actividades__bloque .actividades__informacion h3 {
  text-align: left;
}
.actividades__bloque .actividades__informacion p {
  font-size: 2rem;
  text-align: justify;
}

.contacto {
  padding: 5rem 0;
}

@media (min-width: 768px) {
  .contacto__campos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.contacto__campo--formulario .bloque {
  display: flex;
}
.contacto__campo--formulario .bloque .email {
  flex: 1;
  border: none;
  border-bottom: 0.3rem solid;
}
.contacto__campo--formulario .bloque .email::placeholder {
  letter-spacing: 0.3rem;
}
.contacto__campo--formulario .bloque .bt-enviar {
  border-radius: 1rem;
  border: 0.3rem solid #0e8a96;
  color: #0e8a96;
  padding: 1rem;
  background: transparent;
  margin: 0 2rem;
  flex: 0 0 15rem;
  letter-spacing: 0.3rem;
  transition: background-color 0.6s ease, color 0.5s ease;
}
.contacto__campo--formulario .bloque .bt-enviar:hover {
  cursor: pointer;
}
.contacto__campo--formulario .bloque .bt-enviar:hover {
  background-color: #0e8a96;
  color: #fff;
}

.contacto__campo--informacion p {
  text-align: center;
}

footer {
  background-color: #000;
  color: #fff;
  padding: 5rem 0;
  text-align: center;
}