@use 'base/variables' as v;
@use 'base/mixins' as m;

.ofertas{
    position: relative;
    background-image: url(/build/img/publicidad/bosque.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 60rem;
    
    h2{
        font-size: 5rem;
        line-height: 4.5rem;
        color: v.$blanco;
        margin: 0;
        padding: 8rem 0;
    }
    .subtitulo{
        max-width: 85rem;
        min-width: 25rem;
        margin: 0 auto;
        text-align: center;
        color: v.$verde-oscuro;
        font-size: 3.8rem;
        line-height: 4rem;
        margin: 2rem 0;
    }
}

.overlay{
    width: 100%;
    height: 100%;
    background-color: rgba($color: v.$negro, $alpha: 0.6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ofertas__campos{
    padding: 3rem 0;
    @include m.tablet{
        @include m.grid(2, 4rem);
    }
    @include m.desktop{
        @include m.grid(4, 4rem);
    }
}

.ofertas__campo{
    background-color: rgba($color: v.$verde-claro, $alpha: 0.2);
    padding: 2rem;
    margin-bottom: 2rem;
    border-radius: 2rem;
    color: v.$blanco;
    .descuento{
        font-size: 8rem;
    }
    p{
        color: v.$blanco;
        font-size: 2rem;
        text-align:center;
    }
}