@use 'variables' as v;
@use 'mixins' as m;

html{
    box-sizing: border-box;
    font-size: 62.5%;
}

*,*:before,*:after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente;
    font-size: 1.6rem;
    line-height: 2.8rem;

    @include m.tablet{
        &.scroll-Nav{
            padding-top: 84px;
        }
    }
}

h1,h2,h3,h4{
    font-weight: 700;
    text-align: center;
}

h1{
    font-size: 3.8rem;
}

h2{
    font-size: 3.4rem;
}

h3{
    font-size: 3rem;
}

h4{
    font-size: 2.8rem;
}

p{
    font-size: 2.2rem;
    margin: 0;
    padding: 0;
}

a{
    text-decoration: none;
    margin: 0;
    padding: 0;
}

img{
    max-width: 100%;
}

.container{
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}

.bloque__flecha{
    display: flex;
    justify-content: center;
    margin: 1rem 0;
    a{
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
        background-color: rgba($color: v.$verde-oscuro, $alpha: 0.6);
    }
}