@use 'base/variables' as v;
@use 'base/mixins' as m;

.destinos{
    background-color: v.$verde-oscuro;
    padding: 5rem 0;
    h2,p{
        color: v.$blanco;
        text-align: center;
        padding: 2rem 0;
    }
    .centrar{
        max-width: 75rem;
        margin: 0 auto;
    }

}

.aventuras{
    padding: 3rem 0;
    @include m.tablet{
        @include m.grid(2, 3rem);
    }
    @include m.desktop{
        @include m.grid(4, 3rem);
    }
}

.aventura{
    background-color: rgba($color: v.$blanco, $alpha: .5);
    box-shadow: 5px -10px 2px rgba($color: v.$blanco, $alpha: 0.4);
    padding: 1rem;
    margin-bottom: 3rem;
    border-radius: 2rem;
    display: grid;
    place-items: center;
    p{
        color: v.$negro;
        text-align: center;
    }
    picture{
        img{
            border-radius: 50%;
            width: 20rem;
            height: 20rem;
        }
    }

    .btn{
        @include m.boton(v.$verde-oscuro);
        width: 30%;
        text-align: center;
        font-size: 2.2rem;
        margin-top: 3rem;
    }
}