@use 'base/variables' as v;
@use 'base/mixins' as m;

.oceano__imagen{
    position: relative;
    background-image: url('/build/img//publicidad/lancha.webp');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 80rem;
}

.oceano__informacion{
    position: absolute;
    background: rgba($color: v.$negro, $alpha: 0.4);
    color: v.$blanco;
    border-radius: 2rem;
    padding: 3rem;
    margin-top: 5rem;
    top: 0;
    left: 0;
    right: 0;
    @include m.tablet{
        @include m.grid(2, 2rem)
    }
    .oceano__titulo {
       h3{
        text-align: right;
        font-size: 5rem;
        line-height: 4.4rem;
       }
    }

    .oceano__texto{
        text-align: center;
    }
}