@use 'base/mixins' as m;
@use 'base/variables' as v;
.navegacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 4rem 0;
    gap: 2rem;
    @include m.tablet{
    flex-direction: row;
    margin: 0rem;
    }
    a{  
        font-weight: 700;
        font-size: 2.2rem;
        color: v.$verde-oscuro;
    }
}