@use 'base/mixins' as m;
@use 'base/variables' as v;

.hero{
    position: relative;
    background-image: url('/build/img/aerostaticos-glob.avif');
    background-size: cover;
    background-position: center center;
    height: 90rem;
}

.hero__contenido{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .hero__informacion{
        border-radius: 2rem;
        padding: 3rem;
        margin-top: 15rem;
        height: auto;
        @include m.tablet{
            width: 65rem;
        }
        background-color: rgba($color: #000000, $alpha: 0.7);
        h2{
            color: v.$verde-claro;
            font-size: 5rem;
            line-height: 4rem;
            margin: 0;
            text-align:left ;
            width: 90%;
        }
        p{
            padding: 3rem 0;
            color: #fff;
        }

        .btn{
            @include m.boton(v.$verde-claro);
        }
    }
}