@use 'base/variables' as v;
@use 'base/mixins' as m;

.viajes{
    padding: 5rem 0;
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    @include m.desktop{
        @include m.grid(3, 2rem);
    }
    .viajes__viaje:nth-child(1),
    .viajes__viaje:nth-child(2),
    .viajes__viaje:nth-child(3){
        color: v.$blanco;
        text-align: center;
        margin-bottom: 3rem;
        position: relative;
        background-repeat: repeat;
        background-size: cover;
        background-position: center center;
        height: 70rem;
        .viaje__informacion{
            padding: 2rem;
        }
    }
    .viajes__viaje:nth-child(1){
        background-image: url('/build/img/viajes/viajes1.webp');
        .viaje__informacion{
            position: absolute;
            background-color: rgba($color: #000000, $alpha: 0.4);
            left: 0;
            right: 0;
            bottom: 0;
        }
    }
    .viajes__viaje:nth-child(2){
        background-image: url('/build/img/viajes/viajes2.webp');
        .viaje__informacion{
            position: absolute;
            background-color: rgba($color: #000000, $alpha: 0.4);
            left: 0;
            right: 0;
            top: 0;
        }
    }
    .viajes__viaje:nth-child(3){
        color: v.$negro;
        @include m.tablet{
            height: auto;
            grid-column: 1/3;
        }
        @include m.desktop{
            grid-column: auto;
        }
    }

}

.viajes__viaje{
    h2{

    }

    p{
        margin: 3rem 0;
    }

    .btn{
        margin-top: 2rem;
        @include m.boton(v.$verde-oscuro);
    }
}