@use 'base/variables' as v;
@use 'base/mixins' as m;

.contacto{
    padding: 5rem 0;
}

.contacto__campos{
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
}

.contacto__campo--formulario{
    .bloque{
        display: flex;
        .email{
            flex: 1;
            border: none;
            border-bottom: 0.3rem solid;
            &::placeholder{
                letter-spacing: 0.3rem;
            }
        }
        .bt-enviar{
            @include m.boton(v.$verde-oscuro);
            margin: 0 2rem;
            flex: 0 0 15rem;
            letter-spacing: 0.3rem;
            transition: background-color .6s ease, color .5s ease;
            &:hover{
                background-color: v.$verde-oscuro;
                color: v.$blanco;
            }
        }
    }
}

.contacto__campo--informacion{
    p{
        text-align: center;
    }
}