@use 'base/variables' as v;
@use 'base/mixins' as m;

.experiencia{
    padding: 5rem 0;
    background-color: v.$negro;
    h2{
        color: v.$blanco;
    }
    .experiencias__iconos{
        @include m.tablet{
            @include m.grid(2, 2rem);
            .experiencias__icono:nth-child(3){
                grid-column: 1/3;
           } 
        }
        @include m.desktop{
            @include m.grid(3, 2rem);
            .experiencias__icono:nth-child(3){
                grid-column: auto;
           } 
        }
    }
}

.experiencias__icono{
    display: grid;
    place-items: center;
    picture{
        padding: 3rem;
       img{
        border-radius: 50%;
        width: 25rem;
        height: 25rem; 
       }
    }
}

.experiencia__informacion{
    h3{
        color: v.$blanco;
    }
    .boton{
        text-align: center
    }
    .btn{
        @include m.boton(v.$verde-claro);
        font-size: 2rem;
    }
}

