@use 'base/variables' as v;
@use 'base/mixins' as m;

.aportaciones{
    padding: 5rem;
    background-color: v.$verde-oscuro;

    .aportaciones__bloques{
        @include m.tablet{
            margin-top: -20rem;
            @include m.grid(2, 2rem);
        }
        @include m.desktop{
            @include m.grid(4, 2rem);
        }
    }
}

.aportaciones__bloque{
    z-index: 1;
    background-color: rgba($color: v.$blanco, $alpha: 0.5);
    padding: 2rem;
    margin-bottom: 3rem;
    border-radius: 2rem;
    display: grid;
    place-items: center;
    .icono__img{
        background-color: v.$verde-oscuro;
        padding: 2rem;
        border-radius: 50%;
        img{
            width:5rem;
            height: 5rem;
        }
    }
    .aporatacion__informacion{
        h2{
            text-transform: uppercase;
        }
        p{
            text-align: center;
        }
    }
}