@use 'variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin grid($columnas, $espacio){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espacio;
}

@mixin boton($color){
    border-radius: 1rem;
    border: 0.3rem solid $color;
    color: $color;
    padding: 1rem;
    background: transparent;
    &:hover{
        cursor: pointer;
    }
}