@use 'base/variables' as v;
@use 'base/mixins' as m;

.color-bg{
    background-color: v.$verde-oscuro;
    padding: 5rem;
    height: 40rem
}
.actividades{
    margin-top: -40rem;
    h2{
        color: v.$blanco;
        padding-top: 10rem;
    }
}

.actividades__bloques{
    @include m.tablet{
        @include m.grid(2, 2rem);
        .actividades__bloque:nth-child(3){
            grid-column: 1/3;
        }
    }
    @include m.desktop{
        @include m.grid(3, 2rem);
        .actividades__bloque:nth-child(3){
            grid-column: auto;
        }
    }
}

.actividades__bloque{
    padding: 2rem;
    picture{
        img{
            border-radius: 2rem;
            height: 40rem;
            width: 100%;
        }
    }
    .actividades__informacion{
        h3{
            text-align: left;
            }
        p{
            font-size: 2rem;
            text-align: justify;
            }
    }
}