@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        align-items: center;

        &.nav-fijo{
            background-color: v.$gris;
            color: v.$verde-oscuro;
            box-shadow: 1px 3px 5px v.$negro;
            z-index: 1;            
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
        }
    }

    h1{
        a{
        font-weight: 700;
        font-size: 5rem;
        filter: opacity(90%);
        margin: 2rem 0;
        color:v.$verde-claro ;
        }
    }
}
